.meow_docs section h4,
.meow_docs section h5 {
  padding: 16px 0;
  line-height: 1.5;
}
.meow_docs section h2 {
  font-size: 18px;
}
.meow_docs section ul {
  margin: 10px 0;
  padding-left: 24px;
  list-style: disc;
}
.meow_docs section ul ul{
  margin:0 !important;
}
.meow_docs section ul li {
  line-height: 1.8;
}
.meow_docs section pre {
  margin: 1rem;
  padding: 1rem;
  background-color: #F7F7F7;
  overflow: auto;
}
.meow_docs section pre code {
  padding: 0;
  font-size: 1em;
  border: none;
  border-radius: 0;
  background-color: transparent;
}
.meow_docs section code {
  padding: .3em .5em;
  font-size: .9em;
  vertical-align: middle;
  border: 1px solid #ECECEC;
  border-radius: 2px;
  background-color: #F7F7F7;
}
.meow_docs section code.code_inline {
  padding: 0 .5em;
}
.meow_docs section img {
  vertical-align: middle;
}
.meow_docs section p + p {
  margin-top: 8px;
}
.meow_docs section a {
  color: #6190e8;
}
.meow_docs section table.table {
    border-collapse: collapse;
    width: calc(100% - 2rem);
    background-color: #fff;
    font-size: 14px;
    margin:0.5rem 1rem 45px 1rem;
    line-height: 1.5em;
    overflow: auto;
}
.meow_docs section table.table th:first-child,.meow_docs section table.table td:first-child {
  padding-left: 10px;
}
.meow_docs section table.table th {
  /* border-top: 1px solid #f3f7fa;
  background-color: #FAFBFC;
  border-bottom: 1px solid #f3f7fa;
  padding: 10px 8px; */
  text-align: left;
  white-space: nowrap;
  color: #909399;
  font-weight: 400;
}
.meow_docs section table.table td,.meow_docs section table.table th{
    border-bottom: 1px solid #dcdfe6;
    padding: 10px;
    max-width: 250px;
}
.meow_docs section table.table td {
  color: #606266;
}

.meow_docs input[type="checkbox"]:checked + label
{
  padding-left: 5px;
  text-decoration: line-through;
  -webkit-text-decoration-color: #606266;
          text-decoration-color: #606266;
  color: #909399;
}

